  <div class="report-sheet checklist-sheet">
    <div class="report-container">
      <div class="sheet-header">
        <div class="logo">
          <img src="/img/pttep-logo.png" />
        </div>
        <div class="title">Checklist</div>
        <div class="docno"></div>
      </div>
      <div class="sheet-body" v-for="(item,index) in checklistHeader" :key="item.id">
        <div class="topic-label" style="grid-column: span 5" v-if="index === 0">
          <div
            class="section-label subheader-label"
            id="subheader-info"
            style="grid-column: span 2"
          >
            <label style="color:white">Piping Information</label>
          </div>
        </div>

        <div
          class="section-label subheader-label"
          id="info-table"
          style="grid-column:span 2"
          v-if="index === 0"
        >
          <label id="info-label">Tag Number:</label>
        </div>
        <div class="section-label subheader-label" id="info-table" v-if="index === 0">
          <label id="info-label">{{$route.params.id_tag}}</label>
        </div>
        <div class="section-label subheader-label" id="info-table" v-if="index === 0">
          <label id="info-label">Inspection Date:</label>
        </div>
        <div class="section-label subheader-label" id="info-table" v-if="index === 0">
          <label id="info-label">{{DATE_FORMAT(record.inspection_date) }}</label>
        </div>

        <div class="section-label header-label" style="grid-row: span 1; grid-column: span 3">
          <label>{{ item.header_content }}</label>
        </div>
        <div class="section-label header-label" style="grid-column: span 1">
          <label style="width: 100%; text-align: center">STATUS</label>
        </div>
        <div class="section-label header-label" style="grid-row: span 1">
          <label>NOTE</label>
        </div>

        <!-- LOOP DISPLAY SUB HEADER -->

        <!-- <div class="topic-label">
          <div class="section-label subheader-label" style="grid-column: span 2">
            <label>{{ }}</label>
          </div>
        </div>-->
        <div
          class="topic-item"
          style="grid-column: span 5"
          v-for="item2 in item.topic"
          :key="item2.id"
        >
          <div class="form-item-value label-align-center">
            <label>{{ item2.no}}</label>
          </div>
          <div class="form-item-value" style="grid-column: span 2">
            <label>{{ item2.header_content}}</label>
          </div>
          <div
            :class="{'form-item-selectbox': true, 'bg-red-status': item2.resultShows[0].id_status === 1, 'bg-orange-status': item2.resultShows[0].id_status === 2, 'bg-yellow-status': item2.resultShows[0].id_status === 3, 'bg-green-status': item2.resultShows[0].id_status === 4}"
          >
            <DxSelectBox
              class="custom-selectbox"
              v-model="item2.resultShows[0].id_status"
              :data-source="mdLvl"
              :input-attr="inputAttr"
              display-expr="severity_level"
              value-expr="id_status"
              placeholder="Select"
              @value-changed="UPDATE_RESULT(item2.resultShows[0])"
            />
          </div>
          <div class="form-item-value">
            <textarea
              placeholder="comment..."
              style="min-height: auto; padding: 0"
              v-model="item2.resultShows[0].note"
              @focusout="
                  UPDATE_RESULT(
                    item2.resultShows[0]
                  )
                "
            />
          </div>
        </div>
        <div
          class="topic-label"
          style="grid-column: span 5"
          v-if="index === checklistHeader.length-1"
        >
          <div class="section-label subheader-label" style="grid-column: span 5">
            <label>Visual Inspection Findings:</label>
          </div>
        </div>
        <div
          class="form-item-textarea"
          style="grid-column: span 5"
          v-if="index === checklistHeader.length-1"
        >
          <textarea
            placeholder="Type finding here..."
            v-model="localRemark.finding"
            @focusout="updateRemark($event)"
          />
        </div>
        <div
          class="topic-label"
          style="grid-column: span 5"
          v-if="index === checklistHeader.length-1"
        >
          <div class="section-label subheader-label" style="grid-column: span 5">
            <label>Recommendations:</label>
          </div>
        </div>
        <div
          class="form-item-textarea"
          style="grid-column: span 5"
          v-if="index === checklistHeader.length-1"
        >
          <textarea
            placeholder="Type recommendations here..."
            v-model="localRemark.recommendation"
            @focusout="updateRemark($event)"
          />
        </div>
      </div>
