<template>
  <div
    class="page-container"
    :class="[
      pagePanelHiding == false ? 'page-container' : 'page-container-hide',
    ]"
  >
    <InspectionRecordPanel @showHidePanel="SHOW_HIDE_PANEL" @viewItem="VIEW_ITEM" />
    <div id="page-container-view" class="list-page" v-if="this.id_inspection_record">
      <v-ons-list>
        <v-ons-list-header>
          Visual Checklist of
          <b>{{ DATE_FORMAT(current_view.inspection_date) }}</b>
        </v-ons-list-header>
      </v-ons-list>
      <div v-if="isLoading == false">
        <div v-if="this.checklistList_existance.general == true">
          <checklistGeneric
            :checklistInfo="this.checklistList.generic"
            :record="current_view"
            :remark="this.remark"
            @update-remark="updateRemark"
            v-if="this.checklistList.generic.length > 0"
          />
        </div>

        <div
          class="center-box-wrapper"
          v-if="
            this.id_inspection_record != '' &&
            this.checklistList_existance.general == false
          "
        >
          <v-ons-toolbar-button v-on:click="CREATE_CHECKLIST()">
            <i class="las la-plus"></i>
            <span>Create New Checklist Form</span>
          </v-ons-toolbar-button>
        </div>
        <div class="center-box-wrapper" v-if="this.id_inspection_record == ''">
          <div class="page-content-message-wrapper">
            <i class="las la-search"></i>
            <span>
              Select inspection record
              <br />to view checklist sheet
            </span>
          </div>
        </div>
        <div
          class="hover-button-wrapper"
          v-if="
            this.checklistList.generic.length > 0
          "
        >
          <div class="btn-panel">
            <v-ons-toolbar-button class="btn" v-on:click="DELETE_CHECKLIST()">
              <i class="las la-trash"></i>
            </v-ons-toolbar-button>
            <!-- <v-ons-toolbar-button class="btn">
              <i class="las la-download"></i>
            </v-ons-toolbar-button>-->
          </div>
        </div>
      </div>
      <Loading v-if="isLoading == true" text="Loading" />
    </div>
    <SelectInspRecord v-if="this.id_inspection_record == ''" />
  </div>
</template> 

<script>
//API
import axios from "/axios.js";
import moment from "moment";

//Components
import Loading from "@/components/app-structures/app-loading.vue";
import "devextreme/dist/css/dx.light.css";
import checklistGeneric from "@/views/Applications/Piping/Pages/Checklist/form-generic.vue";
import InspectionRecordPanel from "@/views/Applications/Piping/Pages/inspection-record-panel.vue";
import SelectInspRecord from "@/components/select-insp-record.vue";

//DataGrid

export default {
  name: "ViewChecklistList",
  components: {
    checklistGeneric,
    Loading,
    InspectionRecordPanel,
    SelectInspRecord
  },
  data() {
    return {
      id_tag: this.$route.params.id_tag,
      id_checklist: this.$route.params.id_checklist,
      id_inspection_record: "",
      remark: {},
      checklistList: {
        generic: [],
        ilast_ext: [],
        ilast_int: []
      },
      checklistList_existance: {
        general: false,
        ilast_ext: false,
        ilast_int: false
      },
      inspRecordList: {},
      isLoading: false,
      campaignList: {},
      pagePanelHiding: false,
      current_view: {},
      formDataRemark: {}
    };
  },
  computed: {},
  created() {
    this.$store.commit("UPDATE_CURRENT_PAGENAME", {
      subpageName: "Checklist"
    });
  },
  mounted() {},
  watch: {
    //triggered whenever the route path changes
    $route() {
      this.CLEAR_CURRENT_VIEW();
      this.id_inspection_record = "";
      this.current_view = {};
    }
  },
  methods: {
    VIEW_ITEM(item) {
      this.current_view = {};
      this.current_view = item;
      console.log("VIEW_ITEM: ");
      console.log(item);
      this.CLEAR_CURRENT_VIEW();
      this.id_inspection_record = item.id_inspection_record;

      this.CHECK_EXIST_RESULT_GENERIC(item.id_inspection_record);
    },

    CREATE_CHECKLIST() {
      console.log(
        "==> CREATE NEW CHECKLIST SHEET: id_insp: " + this.id_inspection_record
      );
      this.isLoading = true;
      axios({
        method: "post",
        url:
          "piping-chk-list/add-all-piping-chk-list?id_insp_record=" +
          this.id_inspection_record,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: {
          id_insp_record: this.id_inspection_record
        }
      })
        .then(res => {
          if (res.status == 201) {
            this.CREATE_REMARK();
            console.log(res.data);
            console.log("NEW CHECKLIST SHEET CREATED (generic)");
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    CREATE_REMARK() {
      this.isLoading = true;
      axios({
        method: "post",
        url: "piping-chk-list/add-piping-chk-list-remark",
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: {
          id_inspection_record: this.id_inspection_record,
          finding: "",
          recommendation: ""
        }
      })
        .then(res => {
          if (res.status == 201) {
            console.log(res.data);
            this.FETCH_CHECKLIST_GENERIC(this.id_inspection_record);
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    DELETE_CHECKLIST() {
      this.$ons.notification
        .confirm(
          "Confirm delete checklist sheet? <br/> This operation cannot be undone"
        )
        .then(res => {
          if (res == 1) {
            console.log("DELETE CHECKLIST SHEET: " + this.id_inspection_record);
            this.isLoading = true;
            axios({
              method: "delete",
              url:
                "piping-chk-list/delete-piping-chk-list?id_insp_record=" +
                this.id_inspection_record,
              headers: {
                Authorization:
                  "Bearer " + JSON.parse(localStorage.getItem("token"))
              },
              data: {}
            })
              .then(res => {
                if (res.status == 204) {
                  console.log("CHECKLIST SHEET DELETED (generic)");
                  this.CLEAR_CURRENT_VIEW();
                }
              })
              .catch(error => {
                console.log(error);
              })
              .finally(() => {
                this.isLoading = false;
              });
          }
        });
    },
    updateRemark(newRemark) {
      this.formDataRemark.id = newRemark.id;
      this.formDataRemark.id_inspection_record = this.id_inspection_record;
      this.formDataRemark.finding = newRemark.finding;
      this.formDataRemark.recommendation = newRemark.recommendation;
      axios({
        method: "put",
        url: "piping-chk-list/edit-piping-chk-list-remark?id=" + newRemark.id,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: this.formDataRemark
      })
        .then(res => {
          if (res.status == 200 && res.data) {
            console.log("==> REMARK UPDATED");
          }
        })
        .catch(error => {
          console.log(error);
          this.$ons.notification.alert(
            "Update Failed!<br/>Please try again later"
          );
        })
        .finally(() => {});
    },

    //GENERIC
    CHECK_EXIST_RESULT_GENERIC(id_inspection_record) {
      console.log("CHECK RESULT EXIST (General): " + id_inspection_record);
      this.isLoading = true;
      axios({
        method: "get",
        url: "piping-chk-list/isExist?insp_id=" + id_inspection_record,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: {}
      })
        .then(res => {
          console.log(res);
          if (res.data == true) {
            this.checklistList_existance.general = true;
            this.FETCH_CHECKLIST_GENERIC(id_inspection_record);
          } else {
            this.checklistList_existance.general = false;
            console.log("CHECK RESULT EXIST (General): FALSE");
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    FETCH_CHECKLIST_GENERIC(id_inspection_record) {
      console.log("FECTH INSPECTION RECORD: " + id_inspection_record);
      this.isLoading = true;
      axios({
        method: "get",
        url:
          "piping-chk-list/get-piping-chk-list-insp-id?id_insp_record=" +
          this.id_inspection_record,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: {}
      })
        .then(res => {
          console.log("FETCH_CHECKLIST_GENERAL: id = " + id_inspection_record);
          if (res.status == 200 && res.data) {
            this.checklistList.generic = res.data;
            console.log(this.checklistList.generic);
            this.FETCH_REMARK(id_inspection_record);
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    FETCH_REMARK(id_inspection_record) {
      axios({
        method: "get",
        url:
          "piping-chk-list/get-piping-chk-list-remark-insp-id?id_insp_record=" +
          id_inspection_record,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: {}
      })
        .then(res => {
          //console.log(res.data);
          if (res.status == 200 && res.data) {
            this.checklistList_existance.general = true;
            this.remark = res.data;
            console.log("remark:");
            console.log(this.remark);
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },

    CLEAR_CURRENT_VIEW() {
      this.checklistList = {
        generic: [],
        ilast_ext: [],
        ilast_int: []
      };
      this.checklistList_existance = {
        general: false,
        ilast_ext: false,
        ilast_int: false
      };
    },
    SCROLL_TOP(container_id) {
      document
        .getElementById(container_id)
        .scroll({ top: 0, behavior: "smooth" });
    },
    SHOW_HIDE_PANEL() {
      this.pagePanelHiding = !this.pagePanelHiding;
    },
    DATE_FORMAT(d) {
      return moment(d).format("LL");
    }
  }
};
</script>

